body {
  background-color: $background !important;
  overflow-x: hidden;
}

.highlighted {
  display: flex;
  justify-content: center !important;
  flex-direction: column;
  background-color: rgba($accent, 0.8);
  height: 100vh;
  color: white;
}

.container-fluid {
  padding: 0 !important;
  margin: 0 !important;
  width: 100vw;
}

.center-in-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;

  .row {
    width: 100%;
  }
}
