.work-detail {
  h1 {
    font-size: 24px !important;
    margin: 0;
  }

  .lead {
    font-size: 14px !important;
    white-space: pre-wrap;
  }

  .tech-list {
    font-size: 12px !important;
    font-weight: bold;
    margin: 0;
  }

  &:nth-of-type(even) {
    text-align: right;
  }

  @media (max-width: $phone-big) {
    h1 {
      margin-top: 0 !important;
    }
  }
}
